.editor {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 40px 1fr;
    grid-template-areas: "input-toolbar output-toolbar"
        "input output";
}

.output-wrapper {
    grid-area: output;
}

.input-wrapper {
    grid-area: input;
}

.cm-editor {
    height: 100%;
}